/* ¯¯¯¯¯¯¯¯¯ *\
  UPLOAD
\* ˍˍˍˍˍˍˍˍˍ */
.fr-upload-group .fr-label .fr-hint-text {
  margin-top: 0.75rem;
}

.fr-upload-group .fr-label + .fr-upload {
  margin-top: 1rem;
}

.fr-upload {
  font-size:  0.875rem ;
  line-height:  1.5rem ;
  width: 100%;
  display: inline-flex;
  margin: 0;
}

.fr-upload:disabled::-webkit-file-upload-button {
  outline-style: none;
  cursor: not-allowed;
}

.fr-upload::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
  cursor: pointer;
  margin-right: 0.5rem;
}

/* ¯¯¯¯¯¯¯¯¯ *\
  LOGO
\* ˍˍˍˍˍˍˍˍˍ */
