.fr-connect__login, .fr-connect__brand {
  line-height: 1;
  z-index: 1;
}

.fr-connect__login {
  font-size: 17px;
  height: 17px;
}

.fr-connect__brand {
  font-weight: bold;
  font-size: 18px;
  height: 18px;
}

.fr-connect--plus {
  padding-right: 3rem;
}

.fr-connect--plus::after {
  content: "";
  display: block;
  position: absolute;
  top: 1rem;
  right: 0.75rem;
  bottom: 1rem;
  width: 1.5rem;
  height: 1.5rem;
  background-repeat: repeat-x, repeat-y;
  background-position: 50% 50%, 50% 50%;
  background-size: 21.5% 21.5%, 21.5% 21.5%;
}

.fr-connect--plus .fr-connect__brand {
  margin-right: -10px;
  overflow: hidden;
  width: 100%;
}

.fr-connect-group {
  --text-spacing: 0 0 1rem 0;
}

.fr-connect-group p {
  font-size:  0.75rem ;
  line-height:  1.25rem ;
}

.fr-connect-group .fr-connect + p a {
  font-size:  0.875rem ;
  line-height:  1.5rem ;
}

.fr-connect {
  background-color: var(--background-action-high-blue-france);
  --hover: var(--background-action-high-blue-france-hover);
  --active: var(--background-action-high-blue-france-active);
  color: var(--text-inverted-blue-france);
}

.fr-connect-group .fr-connect + p a {
  color: var(--text-action-high-blue-france);
}

.fr-connect-group p {
  color: var(--text-mention-grey);
}

.fr-connect::after {
  background-image: linear-gradient(0deg, var(--text-inverted-blue-france), var(--text-inverted-blue-france)), linear-gradient(0deg, var(--text-inverted-blue-france), var(--text-inverted-blue-france));
}

.fr-connect:disabled, a.fr-connect:not([href]) {
  background-color: var(--background-disabled-grey);
  --hover: var(--background-disabled-grey-hover);
  --active: var(--background-disabled-grey-active);
  color: var(--text-disabled-grey);
}

.fr-connect:disabled::after, a.fr-connect:not([href])::after {
  background-image: linear-gradient(0deg, var(--text-disabled-grey), var(--text-disabled-grey)), linear-gradient(0deg, var(--text-disabled-grey), var(--text-disabled-grey));
}

/* ¯¯¯¯¯¯¯¯¯ *\
  CARD
\* ˍˍˍˍˍˍˍˍˍ */
.fr-card {
  display: flex;
  flex-direction: column-reverse;
  position: relative;
  --text-spacing: 0 0 1rem;
  --title-spacing: 0 0 0.5rem;
}

.fr-grid-row .fr-card {
  height: 100%;
}

.fr-card:not(.fr-card--no-arrow) .fr-card__body::after {
  font: normal 700 normal 1.5rem/1 dsfr-icon;
  content: "";
  font-weight: 700;
  width: 1.5rem;
  height: 1.5rem;
  margin-top: auto;
  margin-left: auto;
}

.fr-card.fr-enlarge-link:hover .fr-card__img {
  --brightness: calc(100% + var(--brighten) * 10%);
}

.fr-card.fr-enlarge-link:active .fr-card__img {
  --brightness: calc(100% + var(--brighten) * 20%);
}

.fr-card__img {
  position: relative;
  width: 100%;
  filter: brightness(var(--brightness));
}

.fr-card__img::before {
  content: "";
  display: block;
  width: 100%;
  padding-bottom: 56.25%;
}

.fr-card__img img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 50% 50%;
}

.fr-card__body {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  padding: 1.5rem;
}

.fr-card__detail {
  font-size:  0.75rem ;
  line-height:  1.25rem ;
  order: -1;
}

.fr-card__title {
  font-weight:  700 ;
  font-size:  1.125rem ;
  line-height:  1.5rem ;
  font-weight: 700;
}

@media (min-width: 48em) {
  .fr-card__title {
    font-size:  1.25rem ;
    line-height:  1.75rem ;
  }
}

.fr-card__desc {
  font-size:  0.875rem ;
  line-height:  1.5rem ;
}

.fr-card__link {
  width: 100%;
  margin-top: auto;
}

@media (min-width: 48em) {
  .fr-card--horizontal {
    flex-direction: row-reverse;
  }
  .fr-card--horizontal .fr-card__img {
    max-width: 41.666%;
    flex: 1 0 auto;
  }
}

.fr-card {
  background-color: var(--background-default-grey);
  --hover: var(--background-default-grey-hover);
  --active: var(--background-default-grey-active);
  box-shadow: 0 0 0 1px var(--border-default-grey);
}

.fr-card--grey {
  background-color: var(--background-contrast-grey);
  --hover: var(--background-contrast-grey-hover);
  --active: var(--background-contrast-grey-active);
}

.fr-card:not(.fr-card--no-arrow) .fr-card__body::after {
  color: var(--text-action-high-blue-france);
}

.fr-card__detail {
  color: var(--text-mention-grey);
}

.fr-card__title {
  color: var(--text-title-grey);
}

/* ¯¯¯¯¯¯¯¯¯ *\
  QUOTE
\* ˍˍˍˍˍˍˍˍˍ */
.fr-quote {
  position: relative;
  margin: 0;
  padding: 0 0.5rem 2rem 0.5rem;
  background-size: 50% 1px;
  background-repeat: no-repeat;
  background-position: 0.5rem 100%;
}

@media (min-width: 48em) {
  .fr-quote {
    padding: 0 0 0 2rem;
  }
}

@media (min-width: 48em) {
  .fr-quote {
    background-size: 1px 100%;
    background-position: 0 0;
  }
}

.fr-quote::before {
  font: normal 700 normal 2rem/1 dsfr-icon;
  content: "";
  font-weight: 700;
  display: block;
  padding-bottom: 0.5rem;
}

.fr-quote--column {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

@media (min-width: 48em) {
  .fr-quote--column {
    margin-left: 13rem;
  }
}

@media (min-width: 48em) {
  .fr-quote--column {
    min-height: 13rem;
  }
}

.fr-quote--column .fr-quote__author {
  margin-left: 5.25rem;
}

@media (min-width: 48em) {
  .fr-quote--column .fr-quote__author {
    margin-left: 0;
  }
}

.fr-quote--column .fr-quote__source {
  margin-left: 5.25rem;
}

@media (min-width: 48em) {
  .fr-quote--column .fr-quote__source {
    margin-left: 0;
  }
}

.fr-quote--column figcaption {
  min-height: 4.25rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

@media (min-width: 48em) {
  .fr-quote--column figcaption {
    min-height: auto;
  }
}

.fr-quote blockquote {
  margin: 0;
  --text-spacing: 0 0 1rem;
}

.fr-quote blockquote p {
  font-size:  1.25rem ;
  line-height:  2rem ;
  font-weight: 700;
}

.fr-quote__author {
  font-weight: 700;
  font-size:  0.875rem ;
  line-height:  1.5rem ;
  --text-spacing: 0 0 0.25rem;
}

.fr-quote__image {
  width: 4.25rem;
  background-size: cover;
  border-radius: 50%;
  overflow: hidden;
  position: absolute;
  display: flex;
  flex-direction: row;
}

.fr-quote__image img {
  width: 100%;
  height: 100%;
}

@media (min-width: 48em) {
  .fr-quote__image {
    position: absolute;
    top: 50%;
    left: -13rem;
    width: 11.5rem;
    height: 11.5rem;
    margin-top: -5.75rem;
  }
}

.fr-quote cite {
  font-size:  0.75rem ;
  line-height:  1.25rem ;
  font-style: italic;
}

.fr-quote figcaption {
  --ul-type: none;
  --ol-type: none;
  --ul-start: 0;
  --ol-start: 0;
  --xl-block: 0;
  --li-bottom: 0;
  --ol-content: none;
}

.fr-quote figcaption li {
  font-size:  0.75rem ;
  line-height:  1.25rem ;
}

.fr-quote figcaption .fr-quote__source {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.fr-quote figcaption .fr-quote__source li {
  display: inline-block;
}

.fr-quote figcaption .fr-quote__source li:not(:last-child)::after {
  content: "·";
  display: inline-block;
  padding-left: 0.25rem;
  padding-right: 0.25rem;
}

.fr-quote {
  background-image: linear-gradient(0deg, var(--border-default-grey), var(--border-default-grey));
}

.fr-quote::before {
  color: var(--artwork-minor-blue-france);
}

.fr-quote--green-tilleul-verveine::before {
  color: var(--artwork-minor-green-tilleul-verveine);
}

.fr-quote--green-bourgeon::before {
  color: var(--artwork-minor-green-bourgeon);
}

.fr-quote--green-emeraude::before {
  color: var(--artwork-minor-green-emeraude);
}

.fr-quote--green-menthe::before {
  color: var(--artwork-minor-green-menthe);
}

.fr-quote--green-archipel::before {
  color: var(--artwork-minor-green-archipel);
}

.fr-quote--blue-ecume::before {
  color: var(--artwork-minor-blue-ecume);
}

.fr-quote--blue-cumulus::before {
  color: var(--artwork-minor-blue-cumulus);
}

.fr-quote--purple-glycine::before {
  color: var(--artwork-minor-purple-glycine);
}

.fr-quote--pink-macaron::before {
  color: var(--artwork-minor-pink-macaron);
}

.fr-quote--pink-tuile::before {
  color: var(--artwork-minor-pink-tuile);
}

.fr-quote--yellow-tournesol::before {
  color: var(--artwork-minor-yellow-tournesol);
}

.fr-quote--yellow-moutarde::before {
  color: var(--artwork-minor-yellow-moutarde);
}

.fr-quote--orange-terre-battue::before {
  color: var(--artwork-minor-orange-terre-battue);
}

.fr-quote--brown-cafe-creme::before {
  color: var(--artwork-minor-brown-cafe-creme);
}

.fr-quote--brown-caramel::before {
  color: var(--artwork-minor-brown-caramel);
}

.fr-quote--brown-opera::before {
  color: var(--artwork-minor-brown-opera);
}

.fr-quote--beige-gris-galet::before {
  color: var(--artwork-minor-beige-gris-galet);
}

/* ¯¯¯¯¯¯¯¯¯ *\
  BREADCRUMB
\* ˍˍˍˍˍˍˍˍˍ */
/**
un padding de 4px et une marge négative en compensation sont mis en place afin d'éviter de couper le focus.
 */
.fr-breadcrumb {
  margin: 1rem 0 2rem;
  font-size:  0.75rem ;
  line-height:  1.25rem ;
  --text-spacing: 0 0 0.5rem;
  position: relative;
  /**
   * margin-bottom de 2v pour créer cet espace entre chaque ligne
   */
}

@media (min-width: 48em) {
  .fr-breadcrumb {
    margin-bottom: 2.5rem;
  }
}

.fr-breadcrumb__button {
  background-image: var(--underline-img), var(--underline-img);
  background-position: 50% calc(50% + 0.71875em), 50% calc(50% + 0.65625em);
  background-repeat: no-repeat, no-repeat;
  transition: background-size 0s;
  background-size: 0 0.0625em, 100% 0.0625em;
  font-size:  0.75rem ;
  line-height:  1.25rem ;
  margin: 0;
  padding: 0;
  color: inherit;
}

.fr-breadcrumb__button[aria-expanded="true"] {
  display: none;
}

@media (min-width: 48em) {
  .fr-breadcrumb__button {
    display: none;
  }
}

.fr-breadcrumb .fr-collapse {
  margin-left: -4px;
  margin-right: -4px;
  padding-left: 4px;
  padding-right: 4px;
  transform: translateY(-4px);
}

@media (min-width: 48em) {
  .fr-breadcrumb .fr-collapse {
    margin-left: 0;
    margin-right: 0;
    padding-left: 0;
    padding-right: 0;
    transform: none;
    visibility: inherit;
    overflow: visible;
    max-height: initial;
  }
  .fr-breadcrumb .fr-collapse::before {
    content: none;
  }
}

.fr-breadcrumb__list {
  --ul-type: none;
  --ol-type: none;
  --ul-start: 0;
  --ol-start: 0;
  --xl-block: 0;
  --li-bottom: 0;
  --ol-content: none;
  display: inline-flex;
  flex-direction: row;
  flex-wrap: wrap;
  transform: translateY(4px);
}

@media (min-width: 48em) {
  .fr-breadcrumb__list {
    transform: none;
  }
}

.fr-breadcrumb__link {
  position: relative;
  display: inline-flex;
  font-size:  0.75rem ;
  line-height:  1.25rem ;
  margin-bottom: 0.5rem;
  /**
     * flèche séparatrice en font-icon
     */
}

.fr-breadcrumb__link:not([aria-current]) {
  margin-right: 1.5rem;
}

.fr-breadcrumb__link:not([aria-current])::after {
  font: normal 700 normal 1rem/1 dsfr-icon;
  content: "";
  font-weight: 700;
}

.fr-breadcrumb__link:not([aria-current])::after {
  position: absolute;
  top: 0.125rem;
  right: -1.25rem;
  pointer-events: none;
}

.fr-breadcrumb__link[aria-current]:not([href]) {
  pointer-events: none;
  cursor: default;
}

.fr-breadcrumb {
  color: var(--text-mention-grey);
}

.fr-breadcrumb__link[aria-current] {
  color: var(--text-active-grey);
}

/* ¯¯¯¯¯¯¯¯¯ *\
  FORM
\* ˍˍˍˍˍˍˍˍˍ */
.fr-select-group:not(:last-child), .fr-input-group:not(:last-child) {
  margin-bottom: 1.5rem;
}

.fr-select-group .fr-label .fr-hint-text, .fr-input-group .fr-label .fr-hint-text {
  margin-top: 0.25rem;
  margin-bottom: 0.5rem;
}

.fr-label {
  display: block;
  font-size:  1rem ;
  line-height:  1.5rem ;
}

.fr-label + .fr-input,
